@font-face {
  font-family: ByomIcons;
  src: url(./fonts/Byom-Icons.ttf);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
  color: #00ced1;
  font-size: 2.5rem;
  font-weight: 500;
}

h2 {
  color: #ffffff;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 400;
}

h4 {
  color: #00ced1;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 10px;
  margin-top: 0.5em;
}

p {
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 400;
}
